<template>
  <div>
    <h3 v-if="user">{{user.name}}</h3>
    <h3 v-else>User Name</h3>

    <table class="table" v-if="user">
      <tr>
        <th>Username</th>
        <td>:</td>
        <td>{{user.username}}</td>
      </tr>
      <tr>
        <th>Phone</th>
        <td>:</td>
        <td>{{user.phone}}</td>
      </tr>
      <tr>
        <th>Roles</th>
        <td>:</td>
        <td>
          <el-tag
            style="margin:5px"
            v-for="role in user.roles" :key="role.user_id"
            type="info">{{role.name}}</el-tag>
        </td>
      </tr>
      <tr>
        <th>Shops</th>
        <td>:</td>
        <td>
          <el-tag
            style="margin:5px"
            v-for="shop in user.shops" :key="shop.user_id"
            type="info">{{shop.name}}</el-tag>
        </td>
      </tr>
    </table>

    <!-- <el-table></el-table> -->

    <el-row :gutter="8">
      <el-col :xs="{span: 24}" :sm="{span: 24}" :md="{span: 24}" :lg="{span: 24}" :xl="{span: 24}">
        <h4>Transaction</h4>
        <app-line-chart
          :chart-data="transactionStatistic"
          v-if="transactionStatistic !== null"
        ></app-line-chart>
      </el-col>
    </el-row>
  </div>
</template>


<script>
import LineChart from "@/components/Chart/LineChart";

export default {
  components: {
    appLineChart: LineChart,
  },
  data() {
    return {
      loading: false,

      transactionStatistic: null,
      user: null,
    };
  },
  methods: {
    getUserStatistic() {
      this.$store
        .dispatch("FetchUserStatistic", this.$route.params.id)
        .then(res => {
          const data = res.data;
          const messages = data.messages;
          const status_code = data.status_code;
          this.transactionStatistic = {
            chartData: data.data.statistic.transaction_statistic,
            field: "transaction_id",
          };
        });
    },
    getUserDetail() {
      this.loading = true;
      this.$store
        .dispatch("FetchUser", this.$route.params.id)
        .then(res => {
          const data = res.data;
          const messages = data.messages;
          const status_code = data.status_code;
          this.user = data.data.user;
          this.loading = false;
        });
    }
  },
  created() {
    this.getUserStatistic();
    this.getUserDetail();
  }
};
</script>

<style lang="scss">
.table {
  width: 100%;

  tr {
    th, td {
      text-align: left;
      padding: 10px;
      border-bottom: 1px solid #eee;
    }
    td {

    }
  }
}
</style>




